<template>
    <Chart :cdata="cdata" />
</template>

<script>
import Chart from './chartX.vue'
export default {
    data() {
        return {
            cdata: {
                xData: ['人民币', '美元', '欧元', '英镑'],
                seriesData: [
                    { value: 90, name: '人民币' },
                    { value: 6, name: '美元' },
                    { value: 2, name: '欧元' },
                    { value: 2, name: '英镑' },
                ],
                seriesName: '余额'
            },
        }
    },
    components: {
        Chart,
    },
    mounted() {
        this.timeFn()
    },
    beforeDestroy() {
        clearInterval(this.timing)
    },
    methods: {
        timeFn() {
            this.timing = setInterval(() => {
                let tmp1 = Math.ceil(Math.random() * 4) - 1
                if (tmp1 >= 0) {
                    let obj = this.cdata.seriesData[tmp1]
                    obj.value = obj.value + 1
                    this.cdata.seriesData[tmp1] = obj
                }
            }, 2000)
        },
    },
}
</script>

