<template>
    <Chart :cdata="cdata" />
</template>

<script>
import Chart from './chartX.vue'
export default {
    data() {
        return {
            cdata: {
                xData: ['利率2%', '利率3%', '利率4%', '利率5%'],
                seriesData: [
                    { value: 30, name: '利率2%' },
                    { value: 30, name: '利率3%' },
                    { value: 25, name: '利率4%' },
                    { value: 15, name: '利率5%' },
                ],
                seriesName: '利率'
            },
        }
    },
    components: {
        Chart,
    },
    mounted() {
        this.timeFn()
    },
    beforeDestroy() {
        clearInterval(this.timing)
    },
    methods: {
        timeFn() {
            this.timing = setInterval(() => {
                let tmp1 = Math.ceil(Math.random() * 4) - 1
                if (tmp1 >= 0) {
                    let obj = this.cdata.seriesData[tmp1]
                    obj.value = obj.value + 2
                    this.cdata.seriesData[tmp1] = obj
                }
            }, 2000)
        },
    },
}
</script>

