<template>
    <Chart :cdata="cdata" />
</template>

<script>
import Chart from './chart.vue'
export default {
    data() {
        return {
            cdata: {
                height: '320px',
                width: '460px',
                legendData: ['本年', '上年周期'],
                xAxisData: [
                    '1月',
                    '2月',
                    '3月',
                    '4月',
                    '5月',
                    '6月',
                    '7月',
                    '8月',
                    '9月',
                    '10月',
                    '11月',
                    '12月',
                ],
                yAxisData: [
                    {
                        type: 'value',
                        name: '',
                        position: 'right',
                        alignTicks: false,
                        axisLine: {
                            show: true,
                            lineStyle: {
                                color: 'rgb(46,80,149, 0.3)',
                            },
                        },
                        axisLabel: {
                            formatter: '',
                        },
                        splitLine: {
                            show: true, //网格线
                            lineStyle: {
                                color: ['rgb(46,80,149, 0.3)'],
                                width: 1,
                                type: 'solid',
                            },
                        },
                    },
                    {
                        type: 'value',
                        name: '单位：亿元',
                        position: 'left',
                        alignTicks: true,
                        offset: 0,
                        axisLine: {
                            show: true,
                        },
                        axisLabel: {
                            formatter: '{value}',
                        },
                    },
                ],
                seriesData: [
                    {
                        name: '本年',
                        type: 'bar',
                        data: [2.0, 4.9, 7.0, 6.2, 5, 8, 6, 4, 2, 1, 2, 1],
                    },
                    {
                        name: '上年周期',
                        type: 'bar',
                        yAxisIndex: 1,
                        data: [2.6, 5.9, 9.0, 6.4, 5.3, 9, 6, 4, 2, 1, 2, 1],
                    },
                ],
            },
        }
    },
    components: {
        Chart,
    },
    mounted() {
        this.timeFn()
    },
    beforeDestroy() {
        clearInterval(this.timing)
    },
    methods: {
        timeFn() {
            this.timing = setInterval(() => {
                let tmp1 = Math.ceil(Math.random() * 12) - 1;
                let tmp2 = Math.ceil(Math.random() * 12) - 1;
                if (tmp1 >= 0 && tmp2 >= 0) {
                    let cdata = JSON.parse(JSON.stringify(this.cdata));
                    let num = cdata.seriesData[0].data[tmp1]
                    num = num + 0.2;
                    cdata.seriesData[0].data[tmp1] = num;

                    let num2 = cdata.seriesData[1].data[tmp2]
                    num2 = num2 + 0.2;
                    cdata.seriesData[1].data[tmp2] = num2;
                    this.cdata = cdata;
                }
            }, 1000)
        },
    },
}
</script>

<style lang="scss" scoped></style>
