<template>
    <Echart :options="options" :height="cdata.height" :width="cdata.width"></Echart>
</template>

<script>
import Echart from '@/common/echart'
export default {
    data() {
        return {
            options: {},
        }
    },
    components: {
        Echart,
    },
    props: {
        cdata: {
            type: Object,
            default: () => ({}),
        },
    },
    watch: {
        cdata: {
            handler() {
                // const colors = this.cdata.colors
                this.options = {
                    color: ['#3056A5', '#EECF1C'],
                    grid: {
                        right: '15%',
                        left: '15%',
                        bottom: '30%',
                    },
                    legend: {
                        top: '0',
                        data: this.cdata.legendData,
                    },
                    xAxis: [
                        {
                            type: 'category',
                            axisTick: {
                                alignWithLabel: true,
                            },
                            // prettier-ignore
                            data: this.cdata.xAxisData
                        },
                    ],
                    yAxis: this.cdata.yAxisData,
                    series: this.cdata.seriesData,
                }
            },
            immediate: true,
            deep: true,
        },
    },
}
</script>

<style lang="scss" scoped></style>
