<template>
    <div>
        <Echart :options="options" id="centreLeft1Chart" height="300px" width="500px"></Echart>
    </div>
</template>

<script>
import Echart from '@/common/echart'
export default {
    data() {
        return {
            options: {},
        }
    },
    components: {
        Echart,
    },
    props: {
        cdata: {
            type: Object,
            default: () => ({}),
        },
    },
    watch: {
        cdata: {
            handler(newData) {
                this.options = {
                    color: ['#3E6CB4', '#E27932', '#949494', '#F4BA1E'],
                    tooltip: {
                        trigger: 'item',
                        formatter: '{a} <br/>{b} ({d}%)',
                    },
                    toolbox: {
                        show: true,
                    },
                    calculable: true,
                    legend: {
                        orient: 'vertical',
                        icon: 'circle',
                        right: '0%',
                        y: 'center',
                        data: newData.xData,
                        textStyle: {
                            color: '#D1D4D9',
                        },
                    },
                    series: [
                        {
                            name: this.cdata.seriesName,
                            type: 'pie',
                            radius: ['35%', '50%'],
                            center: ['35%', '50%'],
                            avoidLabelOverlap: false,
                            itemStyle: {
                                borderRadius: 10,
                                borderColor: '#D1D4D9',
                                borderWidth: 1,
                            },
                            label: {
                                show: true,
                                color: '#D1D4D9',
                            },
                            labelLine: {
                                show: true,
                                lineStyle: {
                                    color: ['#fff'],
                                },
                            },
                            data: newData.seriesData,
                        },
                    ],
                }
            },
            immediate: true,
            deep: true,
        },
    },
}
</script>

