<template>
    <Chart :cdata="cdata" />
</template>

<script>
import Chart from './chartX.vue'
export default {
    data() {
        return {
            cdata: {
                xData: ['1年以下（含1年）', '1年-3年（含3年）', '3年-5年（含5年）', '5年以上'],
                seriesData: [
                    { value: 50, name: '1年以下（含1年）' },
                    { value: 25, name: '1年-3年（含3年）' },
                    { value: 15, name: '3年-5年（含5年）' },
                    { value: 10, name: '5年以上' },
                ],
                seriesName: '期限分析',
            },
        }
    },
    components: {
        Chart,
    },
    mounted() {
        this.timeFn()
    },
    beforeDestroy() {
        clearInterval(this.timing)
    },
    methods: {
        timeFn() {
            this.timing = setInterval(() => {
                let tmp1 = Math.ceil(Math.random() * 4) - 1
                if (tmp1 >= 0) {
                    let obj = this.cdata.seriesData[tmp1]
                    obj.value = obj.value + 3
                    this.cdata.seriesData[tmp1] = obj
                }
            }, 2000)
        },
    },
}
</script>

