<template>
    <Chart :cdata="cdata" />
</template>

<script>
import Chart from './chart.vue'
export default {
    data() {
        return {
            cdata: {
                height: '320px',
                width: '460px',
                legendData: ['余额'],
                xAxisData: ['单位1','单位2', '单位3','单位4', '单位5', '单位6', '单位7', '单位8', '单位9'],
                yAxisData: [
                    {
                        type: 'value',
                        name: '单位：亿元',
                        position: 'left',
                        alignTicks: true,
                        axisLine: {
                            show: true,
                        },
                        axisLabel: {
                            formatter: '{value}',
                        },
                        splitLine: {
                            show: true, //网格线
                            lineStyle: {
                                color: ['rgb(46,80,149, 0.3)'],
                                width: 1,
                                type: 'solid',
                            },
                        },
                    },
                    
                ],
                seriesData: [
                    {
                        name: '余额',
                        type: 'bar',
                        barWidth : 10,
                        data: [2.0, 4.9, 7.0, 23.2, 10, 12, 15, 9, 20],
                    }
                ],
            },
        }
    },
    components: {
        Chart,
    },
    mounted() {
        this.timeFn()
    },
    beforeDestroy() {
        clearInterval(this.timing)
    },
    methods: {
        timeFn() {
            this.timing = setInterval(() => {
                let tmp1 = Math.ceil(Math.random() * 9) - 1;
                if (tmp1 >= 0) {
                    let cdata = JSON.parse(JSON.stringify(this.cdata));
                    let num = cdata.seriesData[0].data[tmp1]
                    num = num + 1;
                    cdata.seriesData[0].data[tmp1] = num;
                    this.cdata = cdata;
                }
            }, 1000)
        },
    },
}
</script>

<style lang="scss" scoped></style>
